import React, {
  createContext, useReducer, useContext, Dispatch,
} from 'react';
import { I18nLocale } from './I18n';

export type MealType = 'breakfast' | 'lunch' | 'dinner';
interface MealMultiMenu {
  [mealName: string]: string[]
};
type MealMenu = string[] | MealMultiMenu;
type MealByPlace = { [key in MealType]: MealMenu };
type MealPlaces = 'jigok' | 'rist';
type APIDataStruct = {
  loaded: boolean
  locale: I18nLocale
  meals?: { [key in MealPlaces]: MealByPlace }
};

const initialAPIContext: APIDataStruct = { loaded: false, locale: 'ko' };
const APIContext = createContext<APIDataStruct>(initialAPIContext);

type APIAction =
  | { type: 'FETCH'; }
  | { type: 'SET_LOCALE'; locale: I18nLocale };
const apiReducer = (state: APIDataStruct, action: APIAction): APIDataStruct => {
  switch (action.type) {
    case 'SET_LOCALE': {
      return {
        ...state,
        loaded: action.locale === state.locale ? state.loaded : false,
        locale: action.locale,
      };
    }
    case 'FETCH': {
      return state;
    }
    default: {
      return state;
    }
  }
};

type APIDispatch = Dispatch<APIAction>;
const APIDispatchContext = createContext<APIDispatch | undefined>(undefined);

interface Props { children: React.ReactElement | React.ReactElement[] }
export const APIEnabled: React.SFC<Props> = ({ children }: Props) => {
  const [state, dispatch] = useReducer(apiReducer, initialAPIContext);
  return (
    <APIDispatchContext.Provider value={dispatch}>
      <APIContext.Provider value={state}>
        {children}
      </APIContext.Provider>
    </APIDispatchContext.Provider>
  );
};

export const useAPI = () => {
  const state = useContext(APIContext);
  const dispatch = useContext(APIDispatchContext);

  // Locale Setter
  const setLocale = (locale: I18nLocale): void => {
    dispatch({
      type: 'SET_LOCALE',
      locale,
    });
  };

  // Locale Getter
  const getLocale = (): I18nLocale => state.locale;

  // Menu Fetcher
  const fetchMenu = () => {
    if (!state.loaded) {
      dispatch({ type: 'FETCH' });
    }
  };

  // Menu Getter
  const getMenu = (place: MealPlaces, type: MealType): (MealMenu | null) => {
    let menu = null;
    if (state.loaded && state.meals) {
      menu = state.meals[place][type];
    }
    return menu;
  };

  return {
    setLocale, getLocale, fetchMenu, getMenu,
  };
};

export default useAPI;

/* Real API Handlers */

